import { useState, useEffect } from "react";
import { FaHome } from "react-icons/fa";
import { BsChevronDoubleRight } from "react-icons/bs";
import { BsChevronDoubleLeft } from "react-icons/bs";
import { useFormik } from "formik";
import * as yup from "yup";
// import { getCurrentDate, allowFloatInput } from '../../../components/Common/PowerUps/PowerupFunctions'
import {
  getCurrentDate,
  allowFloatInput,
} from "../../../Components/Common/PowerUps/PowerupFunctions";
import {
  inputContainerStyle,
  commonInputStyle,
  inputErrorStyle,
  inputLabelStyle,
} from "../../../Components/Common/CommonTailwind/CommonTailwind";
import PropertyTaxFloorForm from "./PropertyTaxFloorForm";
import { FcHome } from "react-icons/fc";
import pin from "./Media/pin.png";
import { BiAddToQueue } from "react-icons/bi";
import PropertyTaxOwner from "./PropertyTaxOwner";
import { ColorRing } from "react-loader-spinner";
import { ToastContainer, toast } from "react-toastify";
import PropertyApiList from "../../../Components/ApiList/PropertyApiList";
import axios from "axios";
import ApiHeader from "../../../Components/ApiList/ApiHeader";
import "./font.css";
import CitizenApplyApiList from "../../../Components/CitizenApplyApiList";
import BarLoader from "../../../Components/Common/BarLoader";

function PropertyTaxCalculatorForm(props) {
  const [mobileTowerStatusToggle, setMobileTowerStatusToggle] = useState(false);
  const [hoardingStatusToggle, setHoardingStatusToggle] = useState(false);
  const [petrolPumpStatusToggle, setPetrolPumpStatusToggle] = useState(false);
  const [floorData, setfloorData] = useState();
  const [ownerData, setownerData] = useState();
  const [formLevel, setformLevel] = useState(1);
  const [wardList, setwardList] = useState();
  const [occupancyList, setoccupancyList] = useState();
  const [usageList, setusageList] = useState();
  const [constructionList, setconstructionList] = useState();
  const [propertyList, setpropertyList] = useState();
  const [roadList, setroadList] = useState();
  const [ownerList, setownerList] = useState();
  const [floorList, setfloorList] = useState();
  const [loader, setloader] = useState(false);
  const [ulbList, setulbList] = useState();
  const [isLoading, setisLoading] = useState(false);
  const [wardByUlb, setwardByUlb] = useState()
  const [newWardList, setnewWardList] = useState()




  const { masterList, roadTypeList } = PropertyApiList();
  const { api_getAllUlb, api_wardByUlb, api_newWardByOldWard } = CitizenApplyApiList()


  useEffect(() => {
    setloader(true);
    fetchMasterData()
    fetchRoadTypeList()
    fetchULBList()

  }, []);

  const fetchMasterData = () => {
    axios
      .get(masterList, ApiHeader())
      .then((res) => {
        console.log("master list tax calc => ", res?.data);
        setwardList(res?.data?.data?.ward_master);
        setownerList(res?.data?.data?.ownership_types);
        setoccupancyList(res?.data?.data?.occupancy_type);
        setusageList(res?.data?.data?.usage_type);
        setconstructionList(res?.data?.data?.construction_type);
        setpropertyList(res?.data?.data?.property_type);
        setfloorList(res?.data?.data?.floor_type);
        setloader(false);
      })
      .catch((err) => {
        console.log("getting master list error => ", err);
        setloader(false);
      });
  }

  const fetchRoadTypeList = () => {
    axios
      .get(roadTypeList, ApiHeader())
      .then((res) => {
        console.log("road list tax calc => ", res?.data);
        setroadList(res?.data?.data?.data);
        setloader(false);
      })
      .catch((err) => {
        console.log("getting road list error => ", err);
        setloader(false);
      });
  }


  const validationSchema = yup.object({
    ulbId: yup.string().required("Select ULB"),
    wardNo: yup.string().required("Select ward"),
    newWardNo: yup.string().required("Select new ward"),
    ownerShiptype: yup.string().required("Select ownership type"),
    propertyType: yup.string().required("Select property"),
    zone: yup.string().required("Select Zone"),
    plotArea: yup.string().required("Enter plot area"),
    roadWidth: yup.string().required("Enter road width"),
    mobileTowerStatus: yup.string().required("Select mobile tower status"),
    hoardingStatus: yup.string().required("Select hoarding status"),
    petrolPumpStatus: yup.string().required("Select petrol pump status"),
    waterHarvestingStatus: yup
      .string()
      .required("Select water harvesting status"),
    mobileTowerArea: yup
      .string("enter numbers only")
      .when("mobileTowerStatus", {
        is: 1,
        then: yup.string().required("Field is required"),
      })
      .min(1, "enter minimum ")
      .max(10, "Enter max 10 digit"),
    hoardingArea: yup
      .string()
      .when("hoardingStatus", {
        is: 1,
        then: yup.string().required("Field is required"),
      })
      .min(1, "enter minimum ")
      .max(10, "Enter max 10 digit"),
    petrolPumpArea: yup
      .string()
      .when("petrolPumpStatus", {
        is: 1,
        then: yup.string().required("Field is required"),
      })
      .min(1, "enter minimum ")
      .max(10, "Enter max 10 digit"),
    mobileTowerDate: yup.date().when("mobileTowerStatus", {
      is: 1,
      then: yup.date().required("Field is required"),
    }),
    hoardingDate: yup.date().when("hoardingStatus", {
      is: 1,
      then: yup.date().required("Field is required"),
    }),
    petrolPumpDate: yup.date().when("petrolPumpStatus", {
      is: 1,
      then: yup.date().required("Field is required"),
    }),
  });
  const formik = useFormik({
    initialValues: {
      ulbId: "",
      wardNo: "",
      newWardNo: "",
      ownerShiptype: "",
      propertyType: "",
      zone: "",
      plotArea: "",
      roadWidth: "",
      mobileTowerStatus: "",
      hoardingStatus: "",
      petrolPumpStatus: "",
      waterHarvestingStatus: "",
      mobileTowerArea: "",
      hoardingArea: "",
      petrolPumpArea: "",
      mobileTowerDate: getCurrentDate(),
      hoardingDate: getCurrentDate(),
      petrolPumpDate: getCurrentDate(),
    },

    onSubmit: (values, resetForm) => {
      console.log("calculator data at once", values);
      props.collectFormDataFun(values, floorData, ownerData); //sending PropertyTaxCalculatorForm data to parent to store all form data at one container
      props.nextFun(1) //forwarding to next form level
    },
    validationSchema,
  });

  const seleOptions = [
    { option: "one", value: 1 },
    { option: "two", value: 2 },
    { option: "three", value: 3 },
    { option: "four", value: 4 },
    { option: "five", value: 5 },
    { option: "six", value: 6 },
  ];
  const handleChange = (event) => {
    let name = event.target.name;
    let value = event.target.value;

    { name == 'ulbId' && fetchWardByUlb(value) }
    { name == 'wardNo' && fetchNewWardByOldWard(value) }
    {
      name === "mobileTowerStatus" &&
        (value === "1"
          ? setMobileTowerStatusToggle(true)
          : setMobileTowerStatusToggle(false));
    }
    {
      name === "hoardingStatus" &&
        (value === "1"
          ? setHoardingStatusToggle(true)
          : setHoardingStatusToggle(false));
    }
    {
      name === "petrolPumpStatus" &&
        (value === "1"
          ? setPetrolPumpStatusToggle(true)
          : setPetrolPumpStatusToggle(false));
    }

    // //allow restricted inputs
    // { name == 'mobileTowerArea' && formik.setFieldValue("mobileTowerArea", allowFloatInput(value, formik.values.mobileTowerArea, 20)) } //(currentValue,oldValue,max,isCapital)
    // { name == 'hoardingArea' && formik.setFieldValue("hoardingArea", allowFloatInput(value, formik.values.hoardingArea, 20, true)) }
    // { name == 'petrolPumpArea' && formik.setFieldValue("petrolPumpArea", allowFloatInput(value, formik.values.petrolPumpArea, 20)) }
  };

  const getFloorData = (data) => {
    console.log("---floor data collection----", data);
    setfloorData(data);
  };
  const getOwnerData = (data) => {
    console.log("---Owner data collection----", data);
    setownerData(data);
  };

  const submitForm = () => {
    console.log("submitting form funciton");
    formik.handleSubmit();
  };

  const nextQuestion = () => {
    // if(formLevel==1 && formik.values.wardNo ==''){
    //    alert('select ward no')
    //     return
    // }
    // if(formLevel==1 && formik.values.newWardNo==''){
    //     toast.dismiss()
    //     toast.error('Select New Ward No.')
    //     return
    // }
    // if(formLevel==1 && formik.values.ownerShiptype==''){
    //     toast.dismiss()
    //     toast.error('Select Ward No.')
    //     return
    // }
    setformLevel((prev) => prev + 1);
  };

  const wardNoFun = () => {
    if (formik.values.wardNo == "") {
      toast.error("Select ward no.");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const ownerShiptypeFun = () => {
    if (formik.values.ownerShiptype == "") {
      toast.error("Select ownership type");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const hoardingStatusFun = () => {
    if (formik.values.hoardingStatus == "") {
      toast.error("Select select hoarding status");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const waterHarvestingStatusFun = () => {
    if (formik.values.waterHarvestingStatus == "") {
      toast.error("Select water harvesting status");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const newWardNoFun = () => {
    if (formik.values.newWardNo == "") {
      toast.error("Select new ward no.");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const roadWidthFun = () => {
    if (formik.values.roadWidth == "") {
      toast.error("Enter road width");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  // const newWardNoFun = () => {
  //   if (formik.values.ownerShiptype == "") {
  //     toast.error("Select new ward no.");
  //   } else {
  //     setformLevel((prev) => prev + 1);
  //   }
  // }

  // const newWardNoFun = () => {
  //   if (formik.values.ownerShiptype == "") {
  //     toast.error("Select new ward no.");
  //   } else {
  //     setformLevel((prev) => prev + 1);
  //   }
  // }

  // const hoardingStatusFun = () => {
  //   if (formik.values.hoardingStatus == "") {
  //     toast.error("Select new ward no.");
  //   } else {
  //     setformLevel((prev) => prev + 1);
  //   }
  // }

  const mobileTowerStatusFun = () => {
    if (formik.values.mobileTowerStatus == "") {
      toast.error("Select mobile tower status");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const plotAreaFun = () => {
    if (formik.values.plotArea == "") {
      toast.error("Enter plot area");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const zoneFun = () => {
    if (formik.values.zone == "") {
      toast.error("Select zone");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const propertyTypeFun = () => {
    if (formik.values.propertyType == "") {
      toast.error("Select property type");
    } else {
      setformLevel((prev) => prev + 1);
    }
  };

  const fetchULBList = () => {
    setisLoading(true)
    axios.get(api_getAllUlb, ApiHeader())
      .then(function (response) {
        console.log('ulb list. at..', response?.data)
        setulbList(response?.data?.data)
        setisLoading(false)
      })
      .catch(function (error) {
        console.log('ulb list error.... ', error);
        setisLoading(false)
      })
  }

  const fetchWardByUlb = (ulbId) => {
    console.log('before fetch wardby ulb...')
    axios.post(api_wardByUlb, { ulbId: ulbId }, ApiHeader())
      .then(function (response) {
        console.log('wardlist by ulb ....', response?.data?.data)
        setwardByUlb(response.data.data)
        formik.setFieldValue('wardNo', props?.existingPropertyDetails?.data?.data?.ward_mstr_id)

      })
      .catch(function (error) {
        console.log('errorrr.... ', error);
      })
  }
  const fetchNewWardByOldWard = (oldWardId) => {
    let requestBody = {
      oldWardMstrId: oldWardId,
      ulbId: formik.values.ulbId
    }
    console.log('before fetch wardby old ward...', requestBody)

    axios.post(api_newWardByOldWard, requestBody, ApiHeader())
      .then(function (response) {
        console.log('wardlist by oldward list ....', response?.data?.data)
        setnewWardList(response?.data?.data)
        formik.setFieldValue('newWardNo', props?.existingPropertyDetails?.data?.data?.new_ward_mstr_id)
      })
      .catch(function (error) {
        console.log('errorrr.... ', error);
      })
  }

  // const inputContainerStyle2 = `form-group col-span-4 md:col-span-1 mb-6 md:px-4 border-2 border-black`
  const inputContainerStyle2 = `form-group col-span-4 md:col-span-4 mb-6 md:px-4`;

  console.log("floor data at form in state", floorData);

  const containerStyle = `flex flex-col justify-center `
  const labelStyle = `text-gray-600 poppins text-base `
  const inputStyle = `px-2 cursor-pointer py-1 text-base poppins text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md `

  return (
    <>
      <ToastContainer position="top-right" autoClose={2000} />

      {loader && (
        <BarLoader />
      )}

      {/* ===========Heading=============== */}
      <div className="w-full text-center font-serif text-2xl my-6 sm:my-10 font-bold">
        Calculate Your Holding Tax Anytime
      </div>

      {/* ===========Form=================== */}
      <form onSubmit={formik.handleSubmit} onChange={handleChange} className="grid grid-cols-12 bg-white sm:mx-10 my-2 p-4 md:p-10 rounded-md shadow-sm gap-x-10 gap-y-4">

        {/* ===========Ward No.================ */}
        <div className={`form-group col-span-12 sm:col-span-3 `}>
          <label className={`form-label inline-block mb-1 text-gray-600 text-sm font-semibold`}>ULB<small className="mt-1 text-sm font-semibold text-red-600 inline ">* <span ></span> </small></label>
          <select id='basic_details_1' {...formik.getFieldProps('ulbId')} className={`${commonInputStyle} cursor-pointer `}>
            <option value="" >Select</option>
            {
              ulbList?.map((data) => (
                <option value={data.id}>{data.ulb_name}</option>
              ))
            }
          </select>
          <span className="text-red-600 absolute text-xs">{formik.touched.ulbId && formik.errors.ulbId ? formik.errors.ulbId : null}</span>
        </div>
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Ward No
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("wardNo")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>

              {wardByUlb?.map((data) => (
                <option value={data.id}>{data.ward_name}</option>
              ))}
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.wardNo && formik.errors.wardNo
                ? formik.errors.wardNo
                : null}
            </span>
          </div>
        </div>

        {/* ===========New Ward No.============ */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              New Ward No
              <small className=" mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("newWardNo")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>

              {newWardList?.map((data) => (
                <option value={data.id}>{data.ward_name}</option>
              ))}
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.newWardNo && formik.errors.newWardNo
                ? formik.errors.newWardNo
                : null}
            </span>
          </div>
        </div>

        {/* ==========Ownership Type============== */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Ownership Type
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("ownerShiptype")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>
              {ownerList?.map((data) => (
                <option value={data.id}>{data.ownership_type}</option>
              ))}
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.ownerShiptype && formik.errors.ownerShiptype
                ? formik.errors.ownerShiptype
                : null}
            </span>
          </div>
        </div>

        {/* =============Property Type=============== */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle} `}>
              Property Type
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("propertyType")}
              className={`${inputStyle} `}
            >
              <option value="" selected disabled>
                Select
              </option>

              {propertyList?.map((data) => (
                <option value={data.id}>{data.property_type}</option>
              ))}
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.propertyType && formik.errors.propertyType
                ? formik.errors.propertyType
                : null}
            </span>
          </div>
        </div>

        {/* ===============Zone================ */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Zone
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("zone")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>
              <option value="1">Zone-1</option>
              <option value="2">Zone-2</option>
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.zone && formik.errors.zone
                ? formik.errors.zone
                : null}
            </span>
          </div>
        </div>

        {/* ========Plot Area========== */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Plot-Area (in sq.ft.)
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <input
              {...formik.getFieldProps("plotArea")}
              type="text"
              className={`${inputStyle} cursor-text `}
            />
            <span className="text-red-600 text-xs">
              {formik.touched.plotArea && formik.errors.plotArea
                ? formik.errors.plotArea
                : null}
            </span>
          </div>
        </div>

        {/* ============Road Width================ */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Road-Width
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <input
              {...formik.getFieldProps("roadWidth")}
              type="text"
              className={`${inputStyle} cursor-text`}
            />
            <span className="text-red-600 text-xs">
              {formik.touched.roadWidth && formik.errors.roadWidth
                ? formik.errors.roadWidth
                : null}
            </span>
          </div>
        </div>

        {/* =============Rain Water Harvesting================== */}
        <div className="col-span-12 sm:col-span-3">
          <div className={`${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Rainwater harvesting provision ?
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("waterHarvestingStatus")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>
              <option value="0">No</option>
              <option value="1">Yes</option>
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.waterHarvestingStatus &&
                formik.errors.waterHarvestingStatus
                ? formik.errors.waterHarvestingStatus
                : null}
            </span>
          </div>
        </div>

        {/* =============Mobile Towers============ */}
        <div className={!mobileTowerStatusToggle ? `col-span-12 sm:col-span-3` : `col-span-9 grid grid-cols-9 gap-x-10 gap-y-4 bg-gray-50 border-1 border-gray-200 px-4 py-2 rounded-md shadow-md`}>

          <div className={mobileTowerStatusToggle ? `${containerStyle} col-span-3` : `${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Property has Mobile Tower(s) ?
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("mobileTowerStatus")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>
              <option value="0">No</option>
              <option value="1">Yes</option>
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.mobileTowerStatus &&
                formik.errors.mobileTowerStatus
                ? formik.errors.mobileTowerStatus
                : null}
            </span>
          </div>

          {
            mobileTowerStatusToggle && <>

              <div className={`${containerStyle}` + `col-span-3`}>
                <label className={`${labelStyle}`}>
                  <small className=" mt-1 text-sm font-semibold text-red-600 inline ">
                    *
                  </small>
                  Total Area Covered
                </label>
                <input
                  {...formik.getFieldProps("mobileTowerArea")}
                  name="mobileTowerArea"
                  type="number"
                  className={`${inputStyle}`}
                />
                <span className="text-red-600 text-xs">
                  {formik.touched.mobileTowerArea &&
                    formik.errors.mobileTowerArea
                    ? formik.errors.mobileTowerArea
                    : null}
                </span>
              </div>

              <div className={`${containerStyle}` + `col-span-3`}>
                <label className={`${labelStyle}`}>
                  <small className=" mt-1 text-sm font-semibold text-red-600 inline ">
                    *
                  </small>
                  Installation Date
                </label>

                <input
                  {...formik.getFieldProps("mobileTowerDate")}
                  name="mobileTowerDate"
                  type="date"
                  className={`${inputStyle}`}
                  placeholder="dd-mm-yyyy"
                  min={"2021-05-20"}
                  max={"2024-05-25"}
                />
                <span className="text-red-600 text-xs">
                  {formik.touched.mobileTowerDate &&
                    formik.errors.mobileTowerDate
                    ? formik.errors.mobileTowerDate
                    : null}
                </span>
              </div>

            </>
          }



        </div>

        <div className={!mobileTowerStatusToggle && `col-span-3 md:visible hidden w-full`}></div>


        {/* ===============Hoarding Board====================== */}
        <div className={!hoardingStatusToggle ? `col-span-12 sm:col-span-3` : `col-span-9 grid grid-cols-9 gap-x-10 gap-y-4 bg-gray-50 border-1 border-gray-200 px-4 py-2 rounded-md shadow-md`}>

          <div className={hoardingStatusToggle ? `${containerStyle} col-span-3` : `${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Property has Hoarding Board(s) ?
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("hoardingStatus")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>
              <option value="0">No</option>
              <option value="1">Yes</option>
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.hoardingStatus &&
                formik.errors.hoardingStatus
                ? formik.errors.hoardingStatus
                : null}
            </span>
          </div>

          {
            hoardingStatusToggle && <>

              <div className={`${containerStyle}` + `col-span-3`}>
                <label className={`${labelStyle}`}>
                  <small className=" mt-1 text-sm font-semibold text-red-600 inline ">
                    *
                  </small>
                  Total Area Covered
                </label>
                <input
                  {...formik.getFieldProps("hoardingArea")}
                  name="hoardingArea"
                  type="number"
                  className={`${inputStyle}`}
                />
                <span className="text-red-600 text-xs">
                  {formik.touched.hoardingArea &&
                    formik.errors.hoardingArea
                    ? formik.errors.hoardingArea
                    : null}
                </span>
              </div>

              <div className={`${containerStyle}` + `col-span-3`}>
                <label className={`${labelStyle}`}>
                  <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                    *
                  </small>
                  Installation Date
                </label>
                <input
                  {...formik.getFieldProps("hoardingDate")}
                  name="hoardingDate"
                  type="date"
                  className={`${inputStyle}`}
                />
                <span className="text-red-600 text-xs">
                  {formik.touched.hoardingDate &&
                    formik.errors.hoardingDate
                    ? formik.errors.hoardingDate
                    : null}
                </span>
              </div>

            </>
          }



        </div>

        <div className={!hoardingStatusToggle && `col-span-3 md:visible hidden w-full`}></div>


        {/* ============Petrol Pump============= */}
        <div className={!petrolPumpStatusToggle ? `col-span-12 sm:col-span-3` : `col-span-9 grid grid-cols-9 gap-x-10 gap-y-4 bg-gray-50 border-1 border-gray-200 px-4 py-2 rounded-md shadow-md`}>

          <div className={petrolPumpStatusToggle ? `${containerStyle} col-span-3` : `${containerStyle}`}>
            <label className={`${labelStyle}`}>
              Is property a Petrol Pump ?
              <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                *
              </small>
            </label>
            <select
              {...formik.getFieldProps("petrolPumpStatus")}
              className={`${inputStyle}`}
            >
              <option value="" selected disabled>
                Select
              </option>
              <option value="0">No</option>
              <option value="1">Yes</option>
            </select>
            <span className="text-red-600 text-xs">
              {formik.touched.petrolPumpStatus &&
                formik.errors.petrolPumpStatus
                ? formik.errors.petrolPumpStatus
                : null}
            </span>
          </div>

          {
            petrolPumpStatusToggle && <>

              <div className={`${containerStyle}` + `col-span-3`}>
                <label className={`${labelStyle}`}>
                  <small className=" mt-1 text-sm font-semibold text-red-600 inline ">
                    *
                  </small>
                  Total Area Covered
                </label>
                <input
                  {...formik.getFieldProps("petrolPumpArea")}
                  name="petrolPumpArea"
                  type="number"
                  className={`${inputStyle}`}
                />
                <span className="text-red-600 text-xs">
                  {formik.touched.petrolPumpArea &&
                    formik.errors.petrolPumpArea
                    ? formik.errors.petrolPumpArea
                    : null}
                </span>
              </div>

              <div className={`${containerStyle}` + `col-span-3`}>
                <label className={`${labelStyle}`}>
                  <small className="mt-1 text-sm font-semibold text-red-600 inline ">
                    *
                  </small>
                  Installation Date
                </label>
                <input
                  {...formik.getFieldProps("petrolPumpDate")}
                  name="petrolPumpDate"
                  type="date"
                  className={`${inputStyle}`}
                />
                <span className="text-red-600 text-xs">
                  {formik.touched.petrolPumpDate &&
                    formik.errors.petrolPumpDate
                    ? formik.errors.petrolPumpDate
                    : null}
                </span>
              </div>

            </>
          }

        </div>

        <div className={!petrolPumpStatusToggle && `col-span-3 md:visible hidden w-full`}></div>

      </form>

      {/* ================Floor Details================= */}
      <div className="col-span-12 bg-gray-50 px-4 py-2 shadow-md sm:mx-10 my-4 p-4">
        <PropertyTaxFloorForm
          getFloorData={getFloorData}
          usageList={usageList}
          occupancyList={occupancyList}
          constructionList={constructionList}
          floorList={floorList}
        />
      </div>

      {/* ====================Owner Details================= */}
      {/* <div className="col-span-12 bg-gray-50 px-4 py-2 shadow-md sm:mx-10 my-4 p-4">
        <PropertyTaxOwner getOwnerData={getOwnerData} />
      </div> */}

      {/* ===============Buttons======================= */}
      <div className="w-full flex flex-row flex-wrap justify-end mx-4 my-4 p-4 md:p-10">
        <div className="text-right text-sm text-gray-600">
          <button
            type="button"
            onClick={submitForm}
            className=" px-6 py-2.5 bg-green-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-green-800 hover:shadow-lg focus:bg-green-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-green-800 active:shadow-lg transition duration-150 ease-in-out"
          >

            Calculate
            <BsChevronDoubleRight className="inline font-semibold text-sm md:text-lg" />
          </button>
        </div>
      </div>

    </>
  );
}

export default PropertyTaxCalculatorForm;
