import { useState, useEffect } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { TbWebhook } from "react-icons/tb";
import PropertyTaxCalculatorForm from "./PropertyTaxCalculatorForm";
import PropertyTaxReview from "./PropertyTaxReview";
import axios from "axios";
import ApiHeader from "../../../Components/ApiList/ApiHeader";
import PropertyApiList from "../../../Components/ApiList/PropertyApiList";
import BarLoader from "../../../Components/Common/BarLoader";
import BottomErrorCard from "../../../Components/BottomErrorCard";
import FooterLanding from "../../LandingPageUpdate/FooterLanding";
import Footer from "../../LandingDashboard/LandDashNew/Components/Footer";

function PropertyTaxCalculatorIndex() {


  // =================================================================
  // =================================================================
  //
  // Static Data 
  // ==============================
  // ulbId: "2",
  // assessmentType: "1",
  // ward: '50',
  // useType : '2'
  // 
  // =================================================================
  // =================================================================


  const [formIndex, setFormIndex] = useState(1); //formindex specifies type of form like basicdetails at index 1 ...
  const [animateform1, setAnimateform1] = useState("translate-x-0"); //slide animation control state for BasicDetails form
  const [animateform2, setAnimateform2] = useState("pl-20 translate-x-full"); //slide animation control state for PropertyAddressDetails form
  const [allFormData, setAllFormData] = useState({});
  const [submitButtonStatus, setsubmitButtonStatus] = useState(true);
  const [viewLevel, setviewLevel] = useState(1);
  const [reviewData, setreviewData] = useState();
  const [isLoading, setisLoading] = useState(false);
  const [erroState, seterroState] = useState(false);
  const [erroMessage, seterroMessage] = useState(null);

  //assessment type
  const [assTypeText, setAssTypeText] = useState();

  const { calculatePropertyTax } = PropertyApiList();
  const backFun = (formIndex) => {
    // setsubmitButtonStatus(true)
    setviewLevel(1);
  };
  const nextFun = (formIndex) => {
    setviewLevel(2);
  };
  //activating notification if no owner or no floor added
  const notify = (toastData) => {
    setviewLevel(2);
  };

  const collectAllFormData = (basicDetails, floorData, ownerData) => {
    console.log(
      "-----basic details....",
      basicDetails,
      "----floor details...",
      floorData,
      "-----owner data in index----",
      ownerData
    );
    submitCalculatorForm(basicDetails, floorData, ownerData);
  };
  console.log("....all form data at tax calculator....", allFormData);

  const submitCalculatorForm = (basicDetails, floorData, ownerData) => {
    setsubmitButtonStatus(false);
    setisLoading(true);
    console.log("--1--at submitCalculatorForm");
    // activating loader
    const requestBody = {
      ulbId: "2",
      assessmentType: "1",
      // ward: basicDetails?.wardNo,
      ward: '50',
      newWard: basicDetails?.newWardNo,
      ownershipType: basicDetails?.ownerShiptype,
      propertyType: basicDetails?.propertyType,
      zone: basicDetails?.zone,
      isMobileTower: basicDetails?.mobileTowerStatus,//done
      mobileTower: {
        area: basicDetails?.mobileTowerArea,//done
        dateFrom: basicDetails?.mobileTowerArea//done
      },
      isHoardingBoard: basicDetails?.hoardingStatus,//done
      hoardingBoard: {
        area: basicDetails?.hoardingArea,//done
        dateFrom: basicDetails?.hoardingDate,//done
      },
      isPetrolPump: basicDetails?.petrolPumpStatus,//done
      petrolPump: {
        area: basicDetails?.petrolPumpArea,//done
        dateFrom: basicDetails?.petrolPumpDate//done
      },

      isWaterHarvesting: basicDetails?.waterHarvestingStatus,

      roadType: basicDetails?.roadWidth,
      areaOfPlot: basicDetails?.plotArea,
      floor: floorData,
      // owner: ownerData,
    };

    // const requestBody2 = {
    //   ulb_id: 2,
    //   ward: 1,
    //   propertyType: 1,
    //   roadType: 41,
    //   areaOfPlot: 750,
    //   mobileTowerArea: "100",
    //   mobileTowerDate: "2014-10-07",
    //   hoardingArea: "100",
    //   hoardingDate: "2016-10-07",
    //   petrolPumpArea: 100,
    //   petrolPumpDate: "2014-10-07",
    //   isWaterHarvesting: 0,
    //   zone: 1,
    //   floor: [
    //     {
    //       floorNo: 1,
    //       useType: 1,
    //       constructionType: 1,
    //       occupancyType: 2,
    //       buildupArea: 800,
    //       dateFrom: "2022-10-01",
    //       dateUpto: "",
    //     }
    //   ],
    //   owner: [
    //     {
    //       ownerName: "dfsd rt434",
    //       gender: 2,
    //       guardianName: "dfsd",
    //       relation: "S/O",
    //       mobileNo: 1234567890,
    //       email: "demo@gmail.com",
    //       pan: "2022-01-01",
    //       aadhar: "123456789000",
    //       isArmedForce: 1,
    //       isSpeciallyAbled: 0,
    //       dob: "1994-01-01",
    //     },
    //   ],
    // };

    console.log("--2--before fetch in request....", requestBody);
    //    setviewLevel(2)
    //     return
    axios
      .post(calculatePropertyTax, requestBody, ApiHeader())
      .then(function (response) {
        console.log("==3 cacluator tax response===", response?.data);
        if (response?.data?.status) {
          setreviewData(response?.data);
          setviewLevel(2);
        } else {
          activateBottomErrorCard(true, 'Error occurred in submitting form.. Please try again later.')
        }


        // if(response?.status){
        //     nextFun(2)
        // }

        setisLoading(false);
        setsubmitButtonStatus(true);
      })
      .catch(function (error) {
        console.log("== 3 calcualte tax error== ", error);
        activateBottomErrorCard(true, 'Error occurred in submitting form.. Please try again later.')
        setisLoading(false);
        setsubmitButtonStatus(true);
      });
  };

  const activateBottomErrorCard = (state, msg) => {
    seterroMessage(msg)
    seterroState(state)

  }

  return (
    <>
      <ToastContainer position="top-right" autoClose={2000} />
      {isLoading && <BarLoader />}
      {erroState && <BottomErrorCard activateBottomErrorCard={activateBottomErrorCard} errorTitle={erroMessage} />}

      <div className={`${viewLevel == 1 ? "block" : "hidden"} relative`}>
        <PropertyTaxCalculatorForm
          submitButtonStatus={submitButtonStatus}
          isLoading={isLoading}
          collectFormDataFun={collectAllFormData}
          toastFun={notify}
          backFun={backFun}
          nextFun={nextFun}
        />
      </div>
      <div className={`${viewLevel == 2 ? "block" : "hidden"} relative`}>
        <PropertyTaxReview
          reviewData={reviewData}
          collectFormDataFun={collectAllFormData}
          toastFun={notify}
          backFun={backFun}
          nextFun={nextFun}
        />
      </div>

      {(localStorage.getItem('citizen_token') == null || localStorage.getItem('citizen_token') == undefined || localStorage.getItem('citizen_token') == '') ?
        <FooterLanding /> : <Footer />}
    </>
  );
}

export default PropertyTaxCalculatorIndex;
